    search(term) {
        var listOfBooks = this.httpc.get(this.BASE_URL+'/auto' +'?' + term)
        .pipe(
            debounceTime(1000),  
            map(
                (data: any) => {
                    return (
                        data.length != 0 ? data as any[] : [{"BookName": "No Record Found"} as any]
                    );
                }
        ));

        return listOfBooks;  
    }  
