export function Nospace_Valid(): ValidatorFn {

  return (control: AbstractControl): { [key: string]: any } => {
    var isSpace = false;
    if(control.value ==null){
        isSpace = false;
    }
    else{
        if (control.value.trim().length == 0){
        isSpace = true;
        }
        else{
            isSpace = false;
        }
    }
    
    var isValid = !isSpace;
    return isValid ? null : { 'whitespace': true }

  };
}
@Directive({
    selector: '[myNoSpaces]',
    providers: [{ provide: NG_VALIDATORS, useExisting: Nospace_Dir, multi: true }]
})
export class Nospace_Dir implements Validator {

    private validatorFn = Nospace_Valid();
    validate(control: AbstractControl): { [key: string]: any } {
        return this.validatorFn(control);
    }
}

